.App {
  text-align: center;
  min-height: 100vh; /* Ensure the main container takes at least full viewport height */
  position: relative; /* For footer positioning */
  padding-bottom: 30px; /*Space for footer, adjust as needed*/
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.img-responsive {
  max-width: 100%;
  object-fit: contain;
  max-height: 80vh; 
}


.controls {
  display: flex;
  align-items: center; /* Aligns items vertically in the center */
  justify-content: center; /* Centers items horizontally */
  flex-wrap: wrap; /* Allows items to wrap to next line if space runs out */
  gap: 10px; /* Adds space between items */
}

/* Style for the form */
.upload-form {
  text-align: center;
  margin-bottom: 20px;
}

/* Style for the file input */
.upload-form input[type="file"] {
  padding: 10px;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
}

/* Style for the upload button */
.upload-form button {
  padding: 10px 15px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
  margin: 10px;
}

.upload-form button:hover {
  background-color: #45a049;
}

.upload-form button:active {
  transform: scale(0.95);
}

.controls button {
  padding: 10px 15px; /* Adjust padding for size */
  background-color: #4CAF50; /* A pleasant green; change as you like */
  color: white; /* Text color */
  border: none; /* No border */
  border-radius: 5px; /* Rounded corners */
  font-size: 1rem; /* Adjust font size as needed */
  cursor: pointer; /* Cursor changes to a hand symbol when hovered */
  transition: background-color 0.3s, transform 0.3s; /* Smooth transition for hover effects */
}

.controls button:hover {
  background-color: #45a049; /* Slightly darker shade on hover */
}

.controls button:active {
  transform: scale(0.95); /* Slightly scale down the button when clicked */
}

.controls label {
  margin-right: 10px; /* Space between label and slider */
}

/* Additional styling for the rotation slider if needed */
.controls input[type=range] {
  cursor: pointer;
}

.controls{
  margin-bottom: 40px;
}

.image-and-legend-container {
  display: flex;
  justify-content: center; /* Centers children horizontally */
  align-items: flex-start; /* Aligns children at the top */
  width: 100%; /* Full width of its parent */
  max-width: 1200px; /* Adjust this to limit the maximum size */
  margin: 0 auto; /* Auto margin for horizontal centering */
}

.image-container {
  flex: 1; /* Takes up as much space as possible */
  flex-grow: 1; /* Allows it to grow and take necessary space */
  position: relative; /* For absolute positioning of TransformWrapper */
}

.img-legend {
  max-height: 30vh; /* Max height of viewport height to make it responsive */
  flex-shrink: 0; /* Prevents the legend from shrinking */
  width: auto; /* Maintain aspect ratio */
  align-self: flex-start; /* Aligns at the top */
}

.app-footer {
  background-color: #282c34;
  color: white; /* Light text color for contrast */
  text-align: center; /* Center the text */
  padding: 10px 0; /* Padding for spacing */
  margin-top: 40px; /* Top margin as requested */
  position: fixed; /*Fix position to the bottom*/
  bottom: 0; /* Align to bottom */
  width: 100%; /* Full width */
  font-size: 0.9rem; /* Adjust font size as needed */
  height: 40px;
}

.options-dropdown {
  padding: 8px 12px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
  background-color: white;
  color: #333;
  cursor: pointer;
  margin-left: 10px;
  margin-right: 10px;
}

.options-dropdown:focus {
  outline: none;
  border-color: #4CAF50; /* Highlight color when focused */
}

.pad-label{
  margin-right: 10px;
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  position: fixed; /* To overlay on top of other content */
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.5); /* Semi-transparent white background */
  z-index: 1000; /* Ensure it's above other content */
}

.spinner {
  border: 5px solid #f3f3f3; /* Light grey border */
  border-top: 5px solid #3498db; /* Blue border for spinner */
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.download-link {
  display: inline-block;
  padding: 10px 15px;
  margin: 10px 0;
  background-color: #4CAF50;
  color: white;
  border-radius: 5px;
  text-decoration: none;
  text-align: center;
}

.download-link:hover {
  background-color: #45a049;
}